.picker {
  position: relative;
  width: 100%;
  height: 252px;
  overflow: hidden;
  touch-action: none;
  background-color: #F2F2F2;
  -webkit-overflow-scrolling: auto;
  /* 解决在IOS上滚动惯性失效的问题 */
}
.picker-mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  z-index: 3;
  background-image: linear-gradient(180deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.6)), linear-gradient(0deg, rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.6));
  background-position: top, bottom;
  background-size: 100% 108px;
  background-repeat: no-repeat;
  transform: translateZ(1);
}
.picker-indicator {
  width: 100%;
  height: 36px;
  position: absolute;
  left: 0;
  top: 108px;
  z-index: 3;
}
.picker-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  overflow: auto;
  color: #212121;
}
.picker-item {
  height: 36px;
  line-height: 36px;
  text-align: center;
  color: #212121;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.picker-item-disabled {
  color: #999999;
}
