.border-buttom-1px:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #E6E8EE;
  color: #C7C7C7;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.popup-header {
  width: 100%;
  display: flex;
  height: 44px;
  line-height: 44px;
  font-size: 16px;
  background-color: #fbf9fe;
  position: relative;
}
.border-buttom-1px:after {
  border-color: #E6E8EE;
}
.popup-header-cancel,
.popup-header-confirm {
  padding: 9px 15px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup-header-btn-active {
  background-color: #ddd;
}
.popup-header-cancel {
  color: #666666;
}
.popup-header-center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  text-align: center;
  color: #222;
}
.popup-header-confirm {
  color: #4598F0;
}
