.popup {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 10000;
  width: 100%;
  background-color: #EFEFF4;
  will-change: transform;
  transform: translate3d(0, 100%, 0);
  transition: transform 0.3s;
}
.popup-wrapper {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
}
.popup-content {
  display: flex;
  align-items: center;
}
@keyframes slideUp {
  from {
    transform: translate3d(0, 100%, 0);
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
.popup-slide-up {
  animation: slideUp ease 0.3s forwards;
}
@keyframes slideDown {
  from {
    transform: translate3d(0, 0, 0);
  }
  to {
    transform: translate3d(0, 100%, 0);
  }
}
.popup-slide-down {
  animation: slideDown ease 0.3s forwards;
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
.popup-mask-fade-out {
  animation: fadeOut ease 0.3s forwards;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.popup-mask-fade-in {
  animation: fadeIn ease 0.3s forwards;
}
