.popup-content {
  display: flex;
  position: relative;
}
.border-buttom-1px:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #E6E8EE;
  color: #C7C7C7;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
  background: #E6E8EE;
}
.border-buttom-1px:before {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #E6E8EE;
  color: #C7C7C7;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
  background: #E6E8EE;
}
.popup-header {
  position: relative;
  width: 100%;
  display: flex;
  height: 48px;
  line-height: 48px;
  align-items: center;
  font-size: 16px;
  background-color: #FFFFFF;
  justify-content: space-between;
  padding-left: 12px;
  padding-right: 12px;
}
.popup-header .border-buttom-1px:after {
  border-color: #E6E8EE;
}
.popup-header .popup-header-cancel {
  padding: 9px 0px;
  height: 16px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: #999;
  width: 16px;
}
.popup-header .popup-header-btn-active {
  background-color: #E5E5E5;
}
.popup-header .popup-header-title {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex: 1;
  text-align: center;
  color: #212121;
  font-weight: bold;
}
.popup-footer {
  position: relative;
  width: 100%;
  display: flex;
  height: 48px;
  line-height: 48px;
  font-size: 16px;
  background-color: #FFFFFF;
  justify-content: space-between;
  padding-left: 12px;
  padding-right: 12px;
  align-items: center;
}
.popup-footer .border-buttom-1px:before {
  border-color: #E6E8EE;
}
.popup-footer .popup-footer-clearExit,
.popup-footer .popup-footer-confirm,
.popup-footer .popup-footer-confirm-mustInput {
  padding: 9px 0px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 14px;
  color: white;
}
.popup-footer .popup-footer-btn-active {
  background-color: #E5E5E5;
}
.popup-footer .popup-footer-clearExit {
  color: #276FF5;
}
.popup-footer .popup-footer-confirm {
  background-color: #276FF5;
  width: 88px;
  height: 36px;
  border-radius: 4px;
}
.popup-footer .popup-footer-confirm-mustInput {
  background-color: #276FF5;
  flex: 1;
  height: 36px;
  border-radius: 4px;
}
